import { PropsWithChildren } from "react"
import { Link, useLocation } from "react-router-dom"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { Button, ButtonProps, IconButton } from "@rp/react/web/mui/core"

type Props = PropsWithChildren<{
  to: string
  startIcon: ButtonProps["startIcon"]
  sx?: ButtonProps["sx"]
  collapsed?: boolean
  title: string
  openInNewTab?: boolean
  iconSize?: number
}>

const SideBarButtonLink = ({
  startIcon,
  title,
  collapsed,
  children,
  sx,
  to,
  openInNewTab,
  iconSize = 20,
}: Props) => {
  const location = useLocation()
  const match = location.pathname.startsWith(to)
  if (collapsed) {
    return (
      <RPTooltip title={title} placement="right">
        <IconButton
          component={Link}
          to={to}
          target={openInNewTab ? "_blank" : undefined}
          disableRipple
          aria-label={title}
          sx={{
            height: 30,
            borderRadius: 1,
            backgroundColor: match ? "elevation.dp16" : undefined,
            ":hover": {
              backgroundColor: "elevation.dp16",
            },
            px: 0,
            py: 1,
            "& svg": {
              fontSize: iconSize,
              width: iconSize,
              height: iconSize,
            },
          }}
        >
          {startIcon}
        </IconButton>
      </RPTooltip>
    )
  }
  return (
    <Button
      sx={theme => ({
        borderRadius: "4px",
        color: "text.primary",
        px: 2,
        textTransform: "unset",
        fontWeight: match ? 700 : 400,
        justifyContent: "flex-start",
        cursor: match ? "default" : "pointer",
        height: 30,
        fontSize: "16px",
        backgroundColor: match ? theme.palette.overlay.surface : undefined,
        ":hover": {
          backgroundColor: theme.palette.overlay.surface,
        },
        "& svg": {
          width: `${iconSize}px`,
          height: `${iconSize}px`,
        },
        ...sx,
      })}
      startIcon={startIcon}
      component={Link}
      to={to}
      target={openInNewTab ? "_blank" : undefined}
    >
      {children}
    </Button>
  )
}

export { SideBarButtonLink }
